.pb_photo_booth_main{
    background-color: #fff;
     height: 100%;
    width: 100%;
    @media screen and (min-width: 768px) {
        margin: 30px 0px;
    }
    .pb_customization{
        width: 100%;
        height: 100%;
        padding: 0px 20px;
        .pb_user_photo{
            margin-bottom: 20px;
            position: relative;
        }
        img.pb_frames{
            position: absolute;
            width: 100%;
            height: 100%;
        }
    }
    .pb_tabs{
        max-width: 320px;
        margin: 0 auto;
        .pb_tabs_list{
            border-bottom: 1px solid #E1E1E1;
            margin-bottom: 14px;
            display: flex;
            justify-content: space-between;
            gap: 10px;
            button{
                padding: 0px;
                padding-bottom: 12px;
                cursor: pointer;
                color: #545454;
                font-size: 14px;
                font-weight: 600;
                background-color: transparent;
                border: 0px;
                position: relative;
                &.active{
                    &::before{
                        opacity: 1;
                    }
                }
                &::before{
                    content: '';
                    position: absolute;
                    bottom: -2px;
                    left: 0;
                    width: 100%;
                    height: 2px;
                    background: linear-gradient(60deg, #4a66d6, #ce2e93, #f9b061);
                    opacity: 0;
                    transition: opacity 0.3s;
                }            
            }
        }
    }
    .pb_continue_buttons_main{
        width: 100%;
        text-align: center;
        position: absolute;
        bottom: 12px;
        z-index: 1;
        .pb_continue_buttons{
            background: linear-gradient(109deg, #172238, #0B142C, #0B0E1A);
            padding: 17px 12px;
            color: #fff;
            font-size: 14px;
            font-weight: 600;
            cursor: pointer;
            text-align: center;
            width: 100%;
            border:0px;
            border-radius: 0px 0px 40px 40px;
            @media screen and (max-width: 767px) {
                max-width: 320px;
                border-radius: 4px;
            }
        }
    }
    .pb_text{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 6px;
        .pb_text_box{
            cursor: pointer;
            border-radius: 4px;
            object-fit: contain;
            width: 100%;
            background-color: #D9D9D9;
            border: 1px solid transparent;
            text-align: center;
            padding: 12px;
            position: relative;
            input{
                position: absolute;
                width: 100%;
                left: 0px;
                top: 0px;
                height: 100%;
                opacity: 0;
                cursor: pointer;
            }
            &.active{
                border: 1px solid #111111;
            }
            img{
                margin-bottom: 5px;
                height: 24px;
            }
            p{
                margin: 0px;
                font-size: 14px;
                font-weight: 600;
                color: #959595;
                text-transform: capitalize;
            }
        }
    }
}

.pb_photo_booth_h{
    display: flex;
    height: calc(100% - 61px);
    align-items: center;
    flex-direction: column;
    justify-content: center;
    position: relative;
    &.pb_photo_booth_h_center{
        justify-content: center;
    }
    @media screen and (max-width: 767px) {
        overflow: auto;
        padding-bottom: 15px;
        height: calc(100dvh - 61px);
    }
}
.pb_photo_booth_h .pb_cards{
    padding: 0px 20px;
    width: 100%;
    cursor: pointer;
    .subtext{
        margin-top: 0px;
        margin-bottom: 30px;
        text-align: center;
        font-size: 14px;
        font-weight: bold;
        color: #545454;
    }
    .pb_cards_box{
        display: flex;
        flex-direction: column;
        gap: 20px;
        .pb_card {
            background-color: #fff;
            border-radius: 12px;
            padding: 34px 24px 42px;
            text-align: center;
            box-shadow: 0 0px 12px #A1A8B540;
            transition: transform 0.2s;
            width: 100%;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;    
            &:hover{
                transform: translateY(-2px);
            }
            .pb_card_icon{
                width: 44px;
                height: 44px;
                margin: 0 auto 18px;
                background: linear-gradient(109deg,#172238,#0B142C,#0B0E1A);
                border-radius: 8px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .pb_card-title {
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 2px;
                color: #172238;
                margin-top:0px
            }
            .pb_card-description {
                font-size: 12px;
                color: #959595;
                line-height: 1.7;
                margin: 0px;
            }
          }
    }
} 
.pb_photo_booth_h .pb_user_photo{
    //background-color: #2B2929;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 60px;
    overflow: hidden;
    border: 2px dashed #aaa;
    @media screen and (max-width: 767px) {
        position: relative;
        margin: 0 auto;
        margin-bottom: 50px;
        max-width: 320px;
    }
    video{
        object-fit: cover;
    }
    button{
        position: absolute;
        background-color: #0000009e;
        border: 0px;
        height: 42px;
        width: 42px;
        border-radius: 100px;
        bottom: 10px;
        right: 10px;
        img{
            width: 22px;
        }
    }
    &.active_blur{
        canvas{
            filter: blur(2px);
        }
        &::before {
            content: '';
            position: absolute;
            top: 130px;
            left: 140px;
            border: 3px solid #f3f3f3;
            border-radius: 50%;
            border-top: 3px solid transparent;
            width: 30px;
            height: 30px;
            animation: spin 1s linear infinite;
            z-index: 2;
        }
        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform: rotate(360deg);
            }
        }
    }
}       
.pb_photo_booth_h .pb_user_photo_box{
    width: 100%;
    padding: 0px 20px 40px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.pb_user_buttons{
    width: 100%;
    max-width: 320px;
    margin: 0 auto;
    .pb_buttons{
        width: 100%;
    }
}
.pb_photo_booth_h .pb_take_photo{
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 315px;
    margin: 0 auto;
    button{
        margin: 0px;
    }
}
.pb_photo_booth_h .pb_buttons{
    background: #fff;
    border: 1px solid #E1E1E1;
    border-radius: 4px;
    padding: 11px 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #545454;
    font-size: 14px;
    font-weight: 600;
    margin: 0 auto 12px;
    cursor: pointer;
    height: 47px;
    img{
        margin-right: 10px;
    }
    &.heighlight{
        background: #212529;
        color: #fff;
        &:hover{
            background: #424649;
        }
    }
}
.pb_buttons_transparent{
    display: flex;
    align-items: center;
    background: transparent;
    border: 0px;
    cursor: pointer;
    color: #545454;
    font-size: 14px;
    font-weight: 600;
    img{
        margin-right: 10px;
    }
}

.pb_photo_booth_main .pb_header_bar{
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 16px;
    font-weight: 600;
    color: #545454;
    padding: 20px 20px 10px;
    cursor: pointer;
    margin-top: 20px;
    @media screen and (max-width: 767px) {
        margin-top: 0px;
    }
    img{
        margin-right: 5px;
        cursor: pointer;
        object-fit: contain;
    }
}

.pb_photo_booth_box{
    max-width: 380px;
    width: 100%;
    margin: 0 auto;
    height: 736px;
    padding: 7px 4px;
    overflow: hidden;
    border-radius: 60px;
    position: relative;
    @media screen and (max-width: 767px) {
        max-width: 100%;
        height: 100dvh;
        border-radius: 0px;
        border:0px;
        max-height: 100%;
        padding: 0px;
    }
}
.pb_photo_booth_box:before{
    content: '';
    display: block;
    width: 5px;
    height: 40px;
    position: absolute;
    left: 0;
    background-color: #000;
    top: 150px;
    border-radius: 30px;
    @media screen and (max-width: 767px) {
        content: none;
    }
}
.pb_photo_booth_box:after{
    content: '';
    display: block;
    width: 5px;
    height: 40px;
    position: absolute;
    left: 0;
    background-color: #000;
    top: 200px;
    border-radius: 30px;
    @media screen and (max-width: 767px) {
        content: none;
    }
}

.pb_photo_booth_box_inner{
    height:100%;
    background-color: #F6F9FC;
    border-radius: 45px;
    border: 5px solid #000;
    overflow: hidden;
    @media screen and (max-width: 767px) {
        border-radius: 0px;
        border:0px;
    }
}
.pb_photo_booth_box_inner:before{
    content: '';
    display: block;
    width: 5px;
    height: 100px;
    position: absolute;
    right: 0;
    background-color: #000;
    top: 130px;
    border-radius: 30px;
    @media screen and (max-width: 767px) {
        content: none;
    }
}
.pb_photo_booth_box_inner:after{
    content: '';
    display: block;
    width: 80px;
    height: 18px;
    position: absolute;
    left: 50%;
    background-color: #000;
    top: 20px;
    border-radius: 60px;
    transform: translate(-50%, 0px);
    @media screen and (max-width: 767px) {
        content: none;
    }
}

.pb_header_logo{
    margin: auto !important;
}
.pb_viewframe{
    position: absolute;
    img{
        -webkit-filter: drop-shadow(0px 0px 10px #474747);
        filter: drop-shadow(0px 0px 10px #474747);
        animation: pb-scale 0.5s ease-in-out alternate;
    }
    span{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 70px;
        color: #fff;
        text-shadow: 0px 0px 10px #474747;
    }
}

@keyframes pb-scale {
    from {
        transform: scale(0.8);
    }
    to {
        transform: scale(1);
    }
}
.pb_take_photo_crop{
    position: absolute;
    z-index: 9;
    transform: rotate(-45deg);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    gap: 20px;
    img{
        -webkit-filter: drop-shadow(0px 0px 10px #474747);
        filter: drop-shadow(0px 0px 10px #474747);
        &:nth-child(1){
            animation: pb-move-right 0.5s ease-in-out infinite alternate;
        }
        &:nth-child(2){
            animation: pb-move-left 0.5s ease-in-out infinite alternate;
        }
    }
    @keyframes pb-move-left {
        from {
            transform: translateX(-10px);
        }
        to {
            transform: translateX(10px);
        }
    }
    @keyframes pb-move-right {
        from {
            transform: translateX(10px);
        }
        to {
            transform: translateX(-10px);
        }
    }
}
.pb-camera-disabled{
    background-color: #000;
    display: flex;
    flex-direction: column;
    color: #fff;
    text-align: center;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    width: 100%;
    max-width: 320px;
    height: 320px;
    margin:0 auto;
    margin-bottom: 40px;
    overflow: hidden;
    p{
        font-size: 14px;
    }
    .pb_camera_disabled_text_bottom{
        color:#ccc;
    }
    .pb_camera_disabled_text{
        font-weight: 500;
    }
    button{
        background-color: rgb(70, 83, 233);
        border: 0px;
        padding: 6px 12px;
        border-radius: 30px;
        color: #fff;
        font-weight: 600;
        display: flex;
        align-items: center;
        cursor: pointer;
        font-size: 12px;
        img{
            margin-right: 5px;
            width: 14px;
        }
    }
}
.pb_tabs_content .pb_list_items{
    gap: 6px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
.pb_list_images,
.pb_stickers img{
    position: relative;
    display: flex;
    border-radius: 4px;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    border: 1px solid transparent;
    background-color: #e1e1e1;
    overflow: hidden;
}
.pb_list_images .pb_list_frames{
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 1/1;
    position: relative;
    z-index: 1;
    border-radius: 4px;
}
.pb_list_images.active,
.pb_stickers img.active{
    border: 1px solid #111111;
}
.pb_list_user_img{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 1/1;
}
.pb_tabs_content .pb_stickers img{
    object-fit: contain;
    padding: 5px;
}
.pb_tabs_content_box{
    max-height: 200px;
    min-height: 200px;
    overflow: auto;
    &::-webkit-scrollbar{
        width: 0px;
    }
    @media screen and (max-width: 767px) {
        max-height: inherit;
        min-height: auto;
        height: calc(100dvh - 532px);
    }
}
.pb_photo_booth_main .pb_photo_booth_h.pb_photo_booth_h_review{
    justify-content: start;
    border-top: 1px solid #E1E1E1;
    padding: 15px 0px;
    @media screen and (max-width: 767px) {
        padding-bottom: 0px;
    }
}
.pb_add_text{
    text-align: center;
    margin-top: 10px;
}
.pb_add_text p{
    text-align: center;
    display: inline-block;
    border-bottom: 1px solid #000;
    font-size: 14px;
    font-weight: 600;
    padding: 0px 5px;
    cursor: pointer;
}
.pb_tabs_content{
    position: relative;
}
.pb_tabs_content .chrome-picker {
    position: absolute;
    bottom: 0px;
    z-index: 1;
}